.footer{
  padding: 4rem 6rem ;
  background-color: #016a9a;
  color: #fff;
}
.top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: start;
  flex-wrap: wrap;
}
.icons i{
  color: #FFF;
  font-size: 2rem;
  margin-left: 1rem;
}
.icons i:hover{
  color: #0291d3;
}
.bottom{
  padding-top: 2rem;
  text-align: start;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.bottom div{
  display: flex;
  flex-direction: column;
}
.bottom  a{
  text-decoration: none;
  color: #fafafa ;
  padding-bottom: 0.4rem;
  font-size: 1.1rem;
}

.bottom h4{
  font-size: 1.3rem;
  padding: 1rem 0 0.8rem 0;
}

@media screen and (max-width:850px) {
  .footer{
    padding: 4rem 2rem;
  }
  .icons{
    margin-top: 20px;
  }
  .icons i{
    margin: 1rem 1rem 0 0;
  }
  .bottom div{
    width: 50%;
    
  }
}